export default {
    name: "Profile",
    async asyncData({$api, params}) {
        let user = await $api.user.get(params.username);
        return {
            user,
            response: await $api.public_ui.list({user: user.id, page_size: 16}),
        }
    },
    head() {
        return {
            title: this.convertName(this.user)
        }
    }
}
